import React, { useEffect, useState } from "react";
import StepWizard from "react-step-wizard";
import EditBankInfo from "./EditBankInfo";
import EditBasicInfo from "./EditBasicInfo";
import EditDocuments from "./EditDocuments";
import EditMeals from "./EditMeals";
import EditPlans from "./EditPlans";
import RestaurantTabNav from "../RestaurantTabNav";
import axios from "axios";
import { useSelector, useDispatch } from "react-redux";
import { useParams } from "react-router";
import { BASIC_INFO, SET_RESTAURANT } from "../../../../utilities/constants";
export default function EditRestaurant(props) {
  // const restaurant = useSelector((state) => state.restaurant);
  const [status, setStatus] = useState("");
  const [restaurant, setRestaurant] = useState({});
  const dispatch = useDispatch();
  const onStepChange = (step) => {
    // console.log(step);
  };
  const onRadioSelected = (e) => {
    const { value } = e.target;
    const rest = {
      [e.target.name]: value,
    };
    setStatus(value);
    dispatch({ type: BASIC_INFO, payload: rest });
  };
  const onSubmit = async (e) => {
    e.preventDefault();
    const response = await axios.put(
      "/api/newrest/" + id,
      restaurant
    );
    if (response !== null) {
      alert(response.data);
    }
  };
  const { id } = useParams();
  const fetchData = async (id) => {
    const response = await axios.get("/api/newrest/" + id);
    const { data } = await response;
    setRestaurant(data);
    setStatus(data.status);
  };
  useEffect(() => {
    let componentMounted = true;
    if (componentMounted) {
      dispatch({ type: SET_RESTAURANT, payload: restaurant });
    }
    fetchData(id);
    return () => {
      componentMounted = false;
    };
  }, [dispatch, restaurant, id]);

  return (
    <div className="wrapper wrapper-content">
      <div className="col-lg-12">
        <div className="ibox">
          <div className="ibox-title">
            <h5>Edit Restaurant</h5>
          </div>
          <div className="ibox-content">
            <form
              id="form"
              className="wizard-big"
              onSubmit={(e) => onSubmit(e)}
            >
              <div className="row justify-content-end mt-2">
                <div className="form-check form-check-inline">
                  <input
                    className="form-check-input"
                    type="radio"
                    checked={status === "Unapproved" && true}
                    // id="unapproved"
                    name="status"
                    value="Unapproved"
                    onChange={(e) => onRadioSelected(e)}
                  />
                  <label className="form-check-label" htmlFor="unapproved">
                    Unapproved
                  </label>
                </div>
                <div className="form-check form-check-inline">
                  <input
                    className="form-check-input"
                    type="radio"
                    name="status"
                    checked={status === "Inactive" && true}
                    value="Inactive"
                    // id="inactive"
                    onChange={(e) => onRadioSelected(e)}
                  />
                  <label className="form-check-label" htmlFor="inactive">
                    Inactive
                  </label>
                </div>
                <div className="form-check form-check-inline">
                  <input
                    className="form-check-input"
                    type="radio"
                    checked={status === "Active" && true}
                    // id="active"
                    name="status"
                    value="Active"
                    onChange={(e) => onRadioSelected(e)}
                  />
                  <label className="form-check-label" htmlFor="active">
                    Active
                  </label>
                </div>
              </div>
              <br />
              <StepWizard
                isHashEnabled={false}
                nav={<RestaurantTabNav {...props} />}
                onStepChange={onStepChange}
              >
                <EditBasicInfo stepName={"Basic"} hashKey={"basic"} />
                <EditDocuments stepName={"Documents"} hashKey={"docs"} />
                <EditMeals stepName={"Meals"} hashKey={"meals"} />
                <EditPlans stepName={"Plans"} hashKey={"plans"} />
                <EditBankInfo stepName={"Bank Info"} hashKey={"bank_info"} />
              </StepWizard>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}
